@use 'vars' as *;
@use 'fonts' as *;

* {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

section {
    padding: 0 60px;
}

.hidden {
    display: none;
}

.mobile {
    display: none;
}

header {
    /*display: flex;*/
    // Color variables

// Global styles
body {
    font-family: Arial, sans-serif;
    background-color: $white-sand;
    color: $dark-charcoal-gray;
}

// Contact info styles
.contact-info {
    padding: 20px;
    background-color: $silver-pink;
    border-radius: 8px;

    p {
        margin: 0;
        display: flex;
        align-items: center;
        
        a {
            color: $dark-charcoal-gray;
            text-decoration: none;
            margin-right: 15px;
            display: flex;
            align-items: center;

            svg {
                margin-right: 8px;
            }

            &:hover {
                text-decoration: underline;
            }
        }
    }
}

// Logo styles
.logo {
    margin: 20px 0;

    img {
        width: 100%;
        max-width: 200px; // Set max width for the logo
    }
}

// Navigation styles
.tool-bar {
    background-color: $warm-medium-brown;
    padding: 15px;
    
    .navigation {
        list-style: none;
        padding: 0;
        display: flex;
        justify-content: space-around;

        li {
            a {
                color: $white;
                text-decoration: none;
                font-weight: bold;
                
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }

    .pop-up {
        background-color: $dark-charcoal-gray;
        color: $white;
        border: none;
        border-radius: 5px;
        padding: 10px 15px;
        cursor: pointer;

        &:hover {
            background-color: darken($dark-charcoal-gray, 10%);
        }
    }
}
}